import Book from "../book"
import removeAccents from "./removeAccents"
import { bookData } from "../../assets/book"

export default function SearchList({ name }) {
  const searchName = removeAccents(name)
  const regex = RegExp(searchName)
  return (
    <div className="w-full">
      <div className="flex justify-between items-center">
        <p className="text-lg">Hiển thị 1-12 trong 126 kết quả</p>
        <p>Từ khóa tìm kiếm: {name}</p>
      </div>
      <div className="grid grid-cols-3 pt-8">
        {name && bookData.filter(items => regex.test(removeAccents(items.name))).map(items => {
          return (
            <Book key={items.id} id={items.id} photo={items.image} name={items.name} author={items.author} price={items.price} sale={items.sale} />
          )
        })}
      </div>
    </div>
  )
}